<template>
  <div class="register-view">
    <h1 class="title">欢迎加入豆瓣</h1>
    <form method="" action="">
      <div class="form-alias">
        <label>
          <strong>帐号</strong>
          <input
            v-model="alias"
            type="text"
            name="alias"
            placeholder="邮箱 / 手机号">
        </label>
      </div>
      <div class="form-pwd">
        <label>
            <strong>请输入密码</strong>
            <template v-if="passType === 'password'">
              <input
              v-model="password"
              type="password"
              name="password"
              placeholder="密码">
            </template>
            <template v-if="passType === 'text'">
              <input
              v-model="password"
              type="text"
              name="password"
              placeholder="密码">
            </template>
            <span class="showpwd" :class="{show: isShow}" @click="showpwd()"></span>
        </label>
      </div>
      <div class="form-name">
        <label>
          <strong>昵称</strong>
          <input
            v-model="name"
            type="text"
            name="name"
            placeholder="用户名">
        </label>
      </div>
      <div class="form-submit">
        <input class="submit" type="submit" value="立即注册">
      </div>
    </form>
    <div class="footer">
      <div class="agreement">点击「注册」代表你已阅读并同意用户使用协议</div>
      <div class="btns">
        <a href="#">下载豆瓣App</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'register-view',
  data () {
    return {
      isShow: 0,
      passType: 'password',
      alias: '',
      password: '',
      name: ''
    }
  },
  methods: {
    showpwd: function () {
      this.isShow = this.isShow ? 0 : 1
      this.isShow ? this.passType = 'text' : this.passType = 'password'
    }
  }
}
</script>

<style lang="scss" scoped>
.register-view {
  h1 {
    margin: 10% 0 9%;
    font-size: 4rem;
    font-weight: 300;
    color: #42bd56;
    text-align: center;
  }

  form {
    padding: 2rem 1.5rem;

    strong {
      font-size: 1.5rem;
      color: #222;
      display: none;
      margin-bottom: 0.5rem;
    }

    input::placeholder {
      color: #ccc;
    }

    input[type="text"], input[type="password"] {
      display: inline-block;
      width: 100%;
      height: 4.4rem;
      padding: 0 0.8rem;
      box-sizing: border-box;
      font-size: 1.5rem;
      background: #fff;
      border: 0.1rem solid #ccc;
      border-top-left-radius: 0.3rem;
      border-top-right-radius: 0.3rem;
      outline: 0;
    }

    .form-pwd input, .form-name input {
      border-top: 0;
    }

    .form-pwd {
      position: relative;

      .showpwd {
        position: absolute;
        right: 0.2rem;
        top: 0;
        display: block;
        height: 100%;
        width: 3.2rem;
        background-image: url(data:image/png;base64,);
        background-repeat: no-repeat;
        background-position: center center;
        background-size: 2.2rem;
        z-index: 3;
      }

      .showpwd.show {
        background-image: url(data:image/png;base64,);
      }
    }

    .submit {
      cursor: pointer;
      width: 100%;
      padding: 1.2rem 2.6rem;
      margin-top: 1rem;
      font-size: 1.7rem;
      text-align: center;
      color: #fff;
      background: #42bd56;
      border: 0.1rem solid #17AA52;
      border-radius: 0.3rem;
    }
  }

  .footer {
    .agreement {
      font-size: 1.4rem;
      color: #aaa;
      text-align: center;
    }

    .btns {
      margin-top: 4rem;
      text-align: center;
      font-size: 1.5rem;

      a {
        color: #42bd56;
        margin-right: 1.5rem;
      }
    }
  }
}
</style>



